import React, { Component } from 'react';
import Header from './header';
import Navbar from './navbar';
import { Switch, Route } from 'react-router-dom';
import Home from './home';
import Faculty from './faculty';
import Students from './students';
import Projects from './projects';
import Events from './events';
import Publications from './publications';

class App extends Component {
  render() {
    return (
        <div>
            <Header />
            <Navbar />
            <Switch>
                <Route exact path='/csg.iiit.ac.in/' component={Home}/>
                <Route path='/csg.iiit.ac.in/faculty' component={Faculty}/>
                <Route path='/csg.iiit.ac.in/students' component={Students}/>
                <Route path='/csg.iiit.ac.in/projects' component={Projects}/>
                <Route path='/csg.iiit.ac.in/events' component={Events}/>
                <Route path='/csg.iiit.ac.in/publications' component={Publications}/>
            </Switch>
        </div>
    );
  }
}

export default App;



